import { create } from 'ipfs-http-client';

class IPFSEngine {
  constructor(prop) {
    this.ipfsServer = {
      upload:'https://ipfs.infura.io:5001/api/v0',
      gateway:'https://ipfs.infura.io/ipfs/'
    }; // https://ipfs.infura.io/ipfs/QmU8dQVUudEm35B3v42pacp5abBoYwhN38YVDDMt6rYH5R
  }

  //====JsonP process==
  makeid = (length) => {
    let result = '';
    const cs = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const len = cs.length;
    for ( let i = 0; i < length; i++ ) {
      result += cs.charAt(Math.floor(Math.random() *  len));
    }
    return result;
  }

  ipfsUri = (code)=> {
    return  this.ipfsServer.gateway + code;
  }
  uploadData = async (data)=> {
    const me = this;
    return new Promise( async (resolve )=> {
      const dt = JSON.stringify(data);
      const client = create(me.ipfsServer.upload);
      const added = await client.add(dt);
      /*
      const key = 'JP_'+added.path;
      const x = await me.convertBase64(file);
      const y = await client.add(key + `("${x}")`);
      const dt = {key:key, code:y.path, gateway:me.ipfsServer.gateway, created:new Date().getTime()};
      
      resolve(dt);
      */
      resolve(added.path);
    });
  }



}

export default  IPFSEngine;