import React,  { useState, useEffect }  from 'react';
import { Container, Image, Button} from 'react-bootstrap';
import { Engine } from '../comm';

import  {  useEthereum } from '../contracts';
import { useParams, useHistory } from 'react-router'

import { useUtils } from '../customHooks';
import { NftComp, FormMint } from '.';
import Web3 from 'web3';

export default (props)=> {
  const eng = new Engine();
  const { hashLink } = useUtils();
  const { poAddress, tokenId } = useParams('');

  const { mainNet, getRpc, currentWallet,isLocalWalletExist, PONetwork, createContract, imutable, mutable, Ximutable, Xmutable } = useEthereum();

  const [ projectTAddr, setProjectTAddr ] = useState('');
  const [ projectMainUri, setProjectMainUri ] = useState('');
  const [ nfts, setNfts ] = useState([]);
  const [ projectinfo, setProjectinfo ] = useState({});

  const [ tm, setTm ] = useState(0);

  const getProject = ()=>{
    const netProvider = getRpc(mainNet);
    if (!netProvider) return true;

    Ximutable(new Web3(netProvider), {
      name: 'suezHub',
      address: poAddress,
      method : ['uri'],
      data : {uri:[tokenId]}
    },v=>{

     if (v.status === 'success') { 
        console.log(88899, !v.data || !v.data.uri ? '' : v.data.uri);
        setProjectMainUri(!v.data || !v.data.uri ? '' : v.data.uri);
     } else {
       console.log(777,v);
       setProjectMainUri(!v || !v.uri ? '' : v.uri);
     }
    });
  }
  const getNFTs = (testRpc, testContract)=>{
    if (!testRpc) return true;
    Ximutable(new Web3(testRpc), {
      name: 'suezProject',
      address: testContract,
      method : ['_getAllTokensUrls']
    },v=>{
      if (v.status === 'success') { 
        setNfts(!v.data || !v.data._getAllTokensUrls ? [] : v.data._getAllTokensUrls);
      }
    });
  }
  useEffect(()=>{
    if (mainNet && poAddress && !isNaN(tokenId)) {
      getProject();
      setTm(new Date().getTime());
    }
   },[ mainNet, poAddress, tokenId ]);

   useEffect(()=>{
    if (projectMainUri) {
      eng.get(projectMainUri , (v=>{
        const testNet = !v || !v.app || !v.app.myProject ? '' : v.app.myProject.network;
        const testPAddr = !v || !v.app || !v.app.myProject ? '' : v.app.myProject.address;
        setProjectinfo(v)
        getNFTs(getRpc(testNet), testPAddr);
        setProjectTAddr(testPAddr);
      }))
    }
  },[ projectMainUri ]);
  
return <Container fluid={true} className="p-3">
    <Container fluid={true} className="p-3 bg-light rounded" style={{minHeight:'32rem'}}>
      <Container className="float-left p-3 m-3 " style={{width:'6rem'}}>
        <Image src={projectinfo.image} style={{width:'100%'}}/>
      </Container>
      <FormMint contract={projectTAddr} fn={{ getNFTs:  getNFTs}} />
      <Container className="clearfix"></Container>
      <Container fluid={true} className="clearfix text-left">
        {!nfts ? '' : nfts.map((v, k)=><span key={k} >
          {hashLink(`/nftViewer/${poAddress}/${tokenId}/${k}`, <NftComp metaDataUri={v}/>)}</span>)}
      </Container>
      <Container className="clearfix"></Container>
    </Container>
  </Container>
}